import React, { useState } from 'react';
import { TextField, Select, MenuItem, Button, InputLabel, Divider } from '@material-ui/core';
import Autocomplete from '@material-ui/lab/Autocomplete';

import produtos from "../produtos.json";

const a = [
    {
        localidade: "Agencia",
        categoria: "Aplicativo",
        subcategoria: "CY - Mensagem de Erro"
    },
]

export default function CadastroProduto() {

    const [novoProduto, setNovoProduto] = useState({
        localidade: "Agencia",
        categoria: "Aplicativo",
        subcategoria: ""
    });
    const [localidade, setLocalidade] = useState([
        "Agencia",
        "Regional",
        "Corporativo"
    ]);
    const [categoria, setCategoria] = useState([
        "Software",
        "Aplicativo",
        "Hardware",
        "Perifrico"
    ]);

    const handleLocalidade = e => setNovoProduto({ ...novoProduto, localidade: e.target.value });
    const handleCategoria = e => setNovoProduto({ ...novoProduto, categoria: e.target.value });
    const handleNovoProduto = e => setNovoProduto({ ...novoProduto, subcategoria: e.target.value });

    const addNovoProduto = () => {
        console.log(novoProduto);
    }

    return (
        <div className="fluid-container">
            <div className="row">
                <div className="col-md-8 offset-md-2">
                    <h2 className="m-3 text-center">Cadastro de Novos Produtos</h2>
                    <Divider className="my-3" />
                    <div className="form-group">
                        <Autocomplete
                            freeSolo
                            id="localidade"
                            disableClearable
                            options={localidade.map(option => option)}
                            renderInput={params => (
                                <TextField
                                    {...params}
                                    label="Selecione a localidade"
                                    margin="normal"
                                    placeholder="Digite ou selecione uma localidade da lista"
                                    variant="outlined"
                                    onChange={handleLocalidade}
                                    InputProps={{ ...params.InputProps, type: 'search' }}
                                />
                            )}
                        />
                    </div>
                    <div className="form-group">
                        <Autocomplete
                            freeSolo
                            id="categoria"
                            disableClearable
                            options={categoria.map(option => option)}
                            renderInput={params => (
                                <TextField
                                    {...params}
                                    label="Selecione a Categoria"
                                    margin="normal"
                                    variant="outlined"
                                    placeholder="Digite ou selecione uma categoria da lista"
                                    onChange={handleCategoria}
                                    InputProps={{ ...params.InputProps, type: 'search' }}
                                />
                            )}
                        />
                    </div>
                    <div className="form-group">
                        <TextField
                            className="w-100"
                            id="novoProduto"
                            label="Digite o novo produto"
                            margin="normal"
                            variant="outlined"
                            value={novoProduto.subcategoria}
                            onChange={handleNovoProduto}
                        />
                    </div>
                    <div className="form-group text-right">
                        <Button
                            variant="outlined"
                            onClick={addNovoProduto}
                        >
                            Adicionar
                        </Button>
                    </div>
                </div>
            </div>
        </div>
    )
}