import React, { useState, useEffect, useContext } from "react";
import {
  Container,
  TextField,
  Button,
  Divider,
  Chip,
  Typography,
} from "@material-ui/core";
import moment from "moment";
import { Autocomplete } from "@material-ui/lab";
import TextMaskCustom, {
  TextMaskCustomHour,
} from "../components/InputScreen/TextMaskCustom";
import TableWeb from "../components/Table/TableWeb.jsx";
import ProdutividadeRealTime from "../components/ProdutividadeRealTime/ProdutividadeRealTime";
import axios from "axios";
import ProfileContext from "../context/profile/profileContext";
import { useSnackbar } from "notistack";

const tipoAtuacao = ["Direcionado", "FCR", "Pendente"];

const qtd = [];
for (let i = 1; i <= 20; i++) {
  qtd.push(i);
}

export default function InputScreenWeb() {
  const { api, userProfile, dev, getUserProfile } = useContext(ProfileContext);
  const apiURL = dev ? "https://localhost:7376" : "";
  const [chamadosTratados, setChamadosTratados] = useState([]);
  const [produtos, setProdutos] = useState([]);
  const [subProduto, setSubProduto] = useState([]);
  var initialState = {
    data: moment().format("DD/MM/YYYY HH:mm:ss"),
    numeroChamado: "",
    quantidadeChamados: 1,
    categoria: "",
    subcategoria: "",
    inicioTratamento: moment().format("DD/MM/YYYY HH:mm:ss"),
    fimTratamento: "",
    vencimento: "",
    dataAbertura: "",
    tipoAtuacao: tipoAtuacao[0],
  };
  const [values, setValues] = useState(initialState);
  const [tempoCallback, setTempoCallback] = useState("00:00:00");
  const [qtdChamados, setQtdChamados] = useState(0);
  //Pega informaes do usuario logado
  const handleChange = (name) => (event) => {
    setValues({
      ...values,
      [name]: event.target.value,
    });
  };

  //Inicia instancia da notificao
  const { enqueueSnackbar } = useSnackbar();

  const handleAutoCompleteChange = (name) => (event, value) => {
    setValues({
      ...values,
      [name]: value,
    });
    console.log("tipoAtuacao", value);
  };

  //Calcula vencimento do chamado
  useEffect(() => {
    moment().locale("pt-br");
    let vencimento = moment(values.dataAbertura, "DD/MM/YYYY HH:mm:ss")
      .add(1, "hours")
      .format("DD/MM/YYYY HH:mm:ss");
    if (vencimento === "Invalid date") vencimento = "";
    setValues({ ...values, vencimento });
  }, [values.dataAbertura]);

  //Consulta lista de produtos
  useEffect(() => {
    if (api) getUserProfile();
    const getSelect = async () => {
      await axios
        .get(apiURL + "/api/input/getselec")
        .then((resp) => {
          setProdutos(resp.data.dados.produtos.produtos);
        })
        .catch((err) => console.error("Log do erro", err));
    };
    getSelect();
  }, []);

  useEffect(() => {
    getProdutividade();
  }, [userProfile]);

  useEffect(() => {
    console.log("isoWeekday", moment("2020-06-22").isoWeekday()); // Pegar o dia atual
    //dividir SLA por 9,
    // Somar horas de SLA
    //Verificar se passou das 18
  });

  //Busca subprodutos de acordo com o produto selecionado
  useEffect(() => {
    let temp = produtos.filter((item) => item.nome === values.categoria);
    if (temp[0] !== undefined) setSubProduto(temp[0].subprodutos);
  }, [values.categoria]);

  //Salvar chamado
  const handleSubmit = async () => {
    handleClick("info", "Salvando chamado...");
    const data = {
      ...values,
      email: userProfile.email,
      fimTratamento: moment().format("DD/MM/YYYY HH:mm:ss"),
    };
    // settable([...table], data);
    console.log("Dados enviados: ", data);
    await axios
      .post(apiURL + "/api/input/inputweb", data)
      .then((resp) => {
        handleClick("success", "Enviado Chamado");
        setValues({
          ...initialState,
          inicioTratamento: moment().format("DD/MM/YYYY HH:mm:ss"),
        });
      })
      .catch((err) => {
        console.error("Log de erro:", err);
        handleClick("error", "Falha no salvamento");
      });
    setChamadosTratados([...chamadosTratados, data]);
  };

  const handleClick = (variant, message) => {
    enqueueSnackbar(message, { variant });
  };

  useEffect(() => {
    getProdutividade();
  }, [userProfile]);

  const getProdutividade = async () => {
    console.log("pegar produtividade");
    await axios
      .get(
        apiURL +
          `/api/getdados/getprodWeb?analista=${
            userProfile.email
          }&date=${moment().format("DD/MM/YYYY")}`
      )
      .then((resp) => {
        console.log("Quantidade chamados", resp.data.length);
        console.log("Chamados Tratados", resp.data);
        setChamadosTratados(resp.data);
        setQtdChamados(resp.data.length);
      })
      .catch((err) => console.log(err));
  };

  return (
    <div className="container-fluid p-0">
      <nav
        className="border border-light bg-light shadow d-flex justify-content-center"
        style={{ height: "60px" }}
      >
        <Typography variant="h5" className="align-self-center">
          Web
        </Typography>
      </nav>
      <br />
      <div className="container-fluid">
        <Container
          disableGutters={true}
          maxWidth="xl"
          className="container-fluid"
        >
          <ProdutividadeRealTime
            tempoCallBack={tempoCallback}
            quantidadeChamados={qtdChamados}
          />
          <form className="m-2 needs-validation">
            <div className="form-row">
              <div className="col-xl-3 mb-3">
                <TextField
                  className="form-control"
                  size="small"
                  value={values.numeroChamado}
                  error={values.numeroChamado.length < 15 ? true : false}
                  id="numberChamado"
                  label="Chamado"
                  variant="outlined"
                  onChange={handleChange("numeroChamado")}
                />
              </div>
              <div className="col-xl-3 mb-3">
                <Autocomplete
                  id="atuacaoType"
                  size="small"
                  options={tipoAtuacao}
                  getOptionLabel={(option) => option}
                  defaultValue={tipoAtuacao[1]}
                  onChange={handleAutoCompleteChange("tipoAtuacao")}
                  renderTags={(value, getTagProps) =>
                    value.map((option, index) => (
                      <Chip
                        variant="outlined"
                        label={option}
                        size="small"
                        {...getTagProps({ index })}
                      />
                    ))
                  }
                  renderInput={(params) => (
                    <TextField {...params} variant="outlined" label="Status" />
                  )}
                />
              </div>
              <div className="col-xl-3 mb-3">
                <TextField
                  className="form-size"
                  size="small"
                  variant="outlined"
                  label="Data de abertura"
                  value={values.dataAbertura}
                  onChange={handleChange("dataAbertura")}
                  id="formatted-numberformat-input"
                  InputProps={{
                    inputComponent: TextMaskCustom,
                  }}
                />
              </div>
              <div className="col-xl-3 mb-3">
                <TextField
                  className="form-size"
                  size="small"
                  variant="outlined"
                  label="Data Vencimento"
                  value={values.vencimento}
                  onChange={handleChange("vencimento")}
                  id="vencimento"
                  InputProps={{
                    inputComponent: TextMaskCustom,
                  }}
                />
              </div>
            </div>

            <div className="form-row">
              <div className="col-xl-3 mb-3">
                <Autocomplete
                  id="categoria"
                  size="small"
                  options={produtos.map((item) => item.nome)}
                  getOptionLabel={(option) => option}
                  onChange={handleAutoCompleteChange("categoria")}
                  renderTags={(value, getTagProps) =>
                    value.map((option, index) => (
                      <Chip
                        variant="outlined"
                        label={option}
                        size="small"
                        {...getTagProps({ index })}
                      />
                    ))
                  }
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      variant="outlined"
                      label="Categoria"
                    />
                  )}
                />
              </div>

              <div className="col-xl-3 mb-3">
                <Autocomplete
                  id="prod"
                  size="small"
                  options={subProduto}
                  getOptionLabel={(option) => option}
                  onChange={handleAutoCompleteChange("subCategoria")}
                  renderTags={(value, getTagProps) =>
                    value.map((option, index) => (
                      <Chip
                        variant="outlined"
                        label={option}
                        size="small"
                        {...getTagProps({ index })}
                      />
                    ))
                  }
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      variant="outlined"
                      label="Subcategoria"
                    />
                  )}
                />
              </div>
              <div className="col-xl-3">
                <Button
                  className="form-size"
                  variant="outlined"
                  color="primary"
                  size="medium"
                  onClick={handleSubmit}
                >
                  Salvar
                </Button>
              </div>
            </div>
          </form>
          <Divider />
          <div className="row m-2 pt-2">
            <TableWeb rowsArray={chamadosTratados} />
          </div>
        </Container>
      </div>
    </div>
  );
}
