import React, { useState, useContext, useEffect } from "react";
import {
  Grid,
  FormControl,
  TextField,
  Paper,
  makeStyles,
  Button,
  Typography,
  ExpansionPanel,
  ExpansionPanelDetails,
  ExpansionPanelSummary,
  Chip,
} from "@material-ui/core";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import { Autocomplete } from "@material-ui/lab";
import SendIcon from "@material-ui/icons/Send";
import ClearIcon from "@material-ui/icons/Clear";
import PublishIcon from "@material-ui/icons/Publish";
import DispatcherTable from "../components/Table/Dispatcher";
import csvToJson from "../components/csvToJson/csvToJson";
import axios from "axios";
import ProfileContext from "../context/profile/profileContext";
import DialogFrame from "../components/DialogFrame";
import io from "socket.io-client";
import { useSnackbar } from "notistack";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    "& > *": {
      margin: theme.spacing(1),
    },
  },
  m2: {
    "margin-left": "5px",
  },
  p5: {
    padding: "10px",
  },
  select: {
    width: "300px",
    display: "flex",
  },
  vcenter: {
    display: "flex",
  },
  button: {
    display: "flex",
    margin: 10,
  },
  buttonGreen: {
    "margin-top": 22,
    "margin-left": 10,
    width: "100%",
    height: "40px",
    color: "green",
    borderColor: "green",
  },
  buttonRed: {
    "margin-top": 22,
    "margin-left": 20,
    width: "100%",
    height: "40px",
    color: "red",
    borderColor: "red",
  },
  buttons: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  buttonFull: {
    "margin-top": 10,
    width: "100%",
    height: "40px",
  },
  widthFull: {
    width: "100%",
  },
}));

const analista = ["Gabriel", "Joao", "Lucas"];

export default function Dispatcher() {
  const classes = useStyles();
  const { dev } = useContext(ProfileContext);
  const apiSocket = dev
    ? "https://localhost:7376"
    : "https://central-oftools.mybluemix.net";
  const apiURL = dev ? "https://localhost:7376" : "";
  const socket = io(apiSocket);
  const [analistas, setAnalistas] = useState({
    logados: 0,
    deslogados: 0,
    todos: [],
  });
  const [chamados, setChamados] = useState({
    emEspera: 0,
    emTratamento: 0,
  });

  const [openPanel, setOpenPanel] = useState(false);
  const [file, setFile] = useState();
  const [text, settext] = useState("UPLOAD");
  const [cont, setCont] = useState(0);
  const [analistaSelecionado, setAnalistaSelecionado] = useState("");

  //Inicia instancia da notificao
  const { enqueueSnackbar } = useSnackbar();
  //***Funes de alterao dos states***
  const handleClick = (variant, message) => {
    enqueueSnackbar(message, { variant });
  };

  useEffect(() => {
    let time = null;
    let timeChamados = null;
    let socketToMe = null;
    let timeLogou = null;
    let previusCalls = null;
    console.log("cont" + cont);
    // socket.on("connect", () => {
    //   clearTimeout(time);
    //   time = setTimeout(() => {
    //     console.log(`[IO] connect => Nova conexo`);
    //     return () => socket.off("connect", {});
    //   }, 1000);
    // });

    socket.on("socketToMe", function (data) {
      console.log("Chamados recebidos: " + data);
      clearTimeout(socketToMe);
      socketToMe = setTimeout(() => {
        setChamados({
          ...chamados,
          emEspera: parseInt(chamados.emEspera) + parseInt(data),
        });
        socket.emit("chamadosTrat", data);
        // return () => socket.off("socketToMe", data);
      }, 1000);
    });

    socket.on("previusCalls", (data) => {
      console.log("previusCall");
      clearTimeout(previusCalls);
      previusCalls = setTimeout(() => {
        const todos = [];
        data.analista.map((e) => todos.push({ nome: e.nome, email: e.email }));
        console.log("value data");
        console.log(todos);
        setChamados({ ...chamados, emEspera: data.chamados });
        setAnalistas({ ...analistas, logados: data.analista.length, todos });
        // return () => socket.off("previusCalls", data);
      }, 1000);
    });

    socket.on("receivedCalls", (data) => {
      clearTimeout(time);
      time = setTimeout(() => {
        setChamados({ ...chamados, emEspera: data });
        // return () => socket.off("receivedCalls", data);
      }, 1000);
    });

    socket.on("logou", (data) => {
      clearTimeout(timeLogou);
      timeLogou = setTimeout(() => {
        socket.emit("login", data);
        // return () => socket.off("logou", data);
      }, 1000);
    });

    socket.on("logar", (data) => {
      clearTimeout(time);
      time = setTimeout(() => {
        const todos = [];
        data.analista.map((e) => todos.push({ nome: e.nome, email: e.email }));
        console.log("value todos");
        console.log(data);
        setAnalistas({ ...analistas, logados: data.length, todos });
        handleClick(
          "info",
          `Analista ${todos[todos.length - 1]} acabou de logar`
        );
        // return () => socket.off("logar", analista.logados);
      }, 1000);
    });

    socket.emit("consultCall", {});

    socket.on("receive", (data) => {
      clearTimeout(timeChamados);
      timeChamados = setTimeout(() => {
        const todos = [];
        data.analista.map((e) => todos.push({ nome: e.nome, email: e.email }));
        setChamados({ ...chamados, emEspera: data.qtdChamado });
        setAnalistas({ ...analistas, logados: data.analista.length, todos });
        // return () => socket.off("receive", data);
      }, 1000);
    });

    socket.on("tratativa", (data) => {
      console.log("tratativa");
      clearTimeout(timeChamados);
      timeChamados = setTimeout(() => {
        setChamados({
          emTratamento: data.emtratamento,
          emEspera: data.emEspera,
        });
        // return () => socket.off("tratativa", data);
      }, 1000);
    });
    setCont(cont + 1);
  }, []);

  const fileChange = (e) => {
    console.log(e.target.files[0].name);
    let file = e.target.files[0];
    settext(file.name);
    console.log(typeof file);
    if (typeof file !== "undefined") {
      let reader = new FileReader();
      reader.onload = async function (event) {
        let params = {
          csvLen: 0, // Quantas linhas sero utilizadas, sem contar o header. 0 = tamanho do arquivo
          skipCol: 0, // Quantas colunas sero descartadas, contando da primeira.
          delimiter: ";", // Separador das linhas
        };
        let json = await csvToJson(event.target.result, params);
        console.log(json);
        setFile(json);
      };
      reader.readAsText(file);
      e.target.value = null;
    }
  };

  const handleAnalistaSelecionado = (e, value) => {
    setAnalistaSelecionado(value);
    console.log(value);
  };
  const [numeroChamado, setNumeroChamado] = useState("");

  const handleNumeroChamado = (e) => {
    setNumeroChamado(e.target.value);
  };

  const [open, setOpen] = useState(false);
  const [status, setStatus] = useState(false);
  const handleClose = () => {
    setOpen(false);
    setStatus(false);
    deleteFile();
  };
  const [dialogMessage, setDialogMessage] = useState("Disparando chamados...");

  const sendFile = async () => {
    if (file !== undefined) {
      setOpen(true);
      let rowArrayObject = [];
      for (let i = 0; i < file.chamado.length; i++) {
        let data = file.dataAbertura !== "" ? file.dataAbertura[i] : "";
        rowArrayObject.push({
          dataAbertura: data,
          notificacao: parseInt(file.chamado[i]),
          origem: file.abertos[i],
          categoria: file.categoria[i],
          descricao: file.descricao[i],
          tempo: file.tempo[i],
          reaberto: file.reaberto[i],
          tratado: false,
          prioridade: 1,
          analistaAtual: file.analista[i],
        });
      }
      console.log(rowArrayObject);
      if (file !== "") {
        await axios
          .post(apiURL + "/api/input/inputDispatcher", rowArrayObject)
          .then((resp) => {
            setStatus(true);
            setDialogMessage("Chamados disparados!");
          })
          .catch((err) => console.log(err.message));
      }
    }
  };

  const handleOpenPanel = () => {
    setOpenPanel(!openPanel);
  };

  const deleteFile = () => (
    setFile(), settext("UPLOAD"), setDialogMessage("Disparando chamados...")
  );

  const sendManual = async () => {
    let obj = [
      {
        notificacao: numeroChamado,
        analistaAtual: analistaSelecionado.email,
        prioridade: 2,
        tratado: false,
      },
    ];
    console.log(obj);
    await axios
      .post(apiURL + "/api/input/inputDispatcher", obj)
      .then(
        (resp) => (
          handleClick("success", "Chamado disparado"), setNumeroChamado("")
        )
      )
      .catch((err) => handleClick("error", "Falha ao disparar chamado"));
  };

  return (
    <div className="container-fluid p-0">
      <nav
        className="border border-light bg-light shadow d-flex justify-content-center"
        style={{ height: "60px" }}
      >
        <Typography variant="h5" className="align-self-center">
          Dispatcher
        </Typography>
      </nav>
      <br />
      <div className="container-fluid">
        <DialogFrame
          open={open}
          onClose={handleClose}
          status={status}
          message={dialogMessage}
        />

        <Grid container spacing={4}>
          <Grid
            item
            sm={10}
            md={6}
            // alignItems="center"
            className={classes.vcenter}
          >
            <FormControl variant="outlined" className={classes.widthFull}>
              <Autocomplete
                className={classes.select + classes.widthFull}
                id="combo-box-demo"
                options={analistas.todos}
                getOptionLabel={(option) => option.nome}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    label="Logon Analista"
                    variant="outlined"
                  />
                )}
              />
            </FormControl>
          </Grid>
          <Grid item sm={12} md={4} className="pt-4">
            <Paper className={classes.p5}>
              <Typography>Analistas Logados: {analistas.logados}</Typography>
              <Typography>
                Chamados em Tratamento: {chamados.emTratamento}
              </Typography>
              <Typography>Chamados em Espera: {chamados.emEspera}</Typography>
            </Paper>
          </Grid>
          <Grid
            container
            item
            md={12}
            sm={12}
            justify="center"
            alignItems="center"
            direction="row"
          >
            <Grid item xs={3}>
              <input
                style={{ display: "none" }}
                accept=".csv"
                id="contained-button-file"
                type="file"
                onChange={fileChange}
              />
              <label
                htmlFor="contained-button-file"
                className={classes.buttonFull}
              >
                <Button
                  variant="outlined"
                  color="primary"
                  className={classes.buttonFull}
                  component="span"
                  size="small"
                >
                  {text} <PublishIcon className={classes.m2} />
                </Button>
              </label>
            </Grid>
            <Grid item xs={3}>
              <Button
                variant="outlined"
                color="primary"
                className={classes.buttonGreen}
                onClick={sendFile}
              >
                ENVIAR <SendIcon className={classes.m2} />
              </Button>
            </Grid>
            <Grid item xs={3}>
              <Button
                variant="outlined"
                color="primary"
                className={classes.buttonRed}
                onClick={deleteFile}
              >
                LIMPAR <ClearIcon className={classes.m2} />
              </Button>
            </Grid>
          </Grid>
          <Grid item md={12} sm={12} className="pt-4">
            <ExpansionPanel expanded={openPanel} onChange={handleOpenPanel}>
              <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                <Typography>Inserir chamado prioriza</Typography>
              </ExpansionPanelSummary>
              <ExpansionPanelDetails>
                <div className="container">
                  <div className="row">
                    <div className="col-5 mb-1">
                      <TextField
                        variant="outlined"
                        label="Nmero do Chamado"
                        className="w-100"
                        onChange={handleNumeroChamado}
                        required
                      ></TextField>
                    </div>
                    <div className="col-5 mb-1">
                      <Autocomplete
                        id="analista"
                        options={analistas.todos}
                        getOptionLabel={(option) => option.nome}
                        renderTags={(value, getTagProps) =>
                          value.map((option, index) => (
                            <Chip
                              variant="outlined"
                              label={option}
                              {...getTagProps({ index })}
                            />
                          ))
                        }
                        renderInput={(params) => (
                          <TextField
                            {...params}
                            variant="outlined"
                            label="Analista"
                          />
                        )}
                        onChange={handleAnalistaSelecionado}
                      />
                    </div>
                    <div className="col-2 mb-1 d-flex align-items-center">
                      <Button
                        variant="outlined"
                        color="secondary"
                        onClick={sendManual}
                      >
                        ENVIAR <SendIcon className={classes.m2} />
                      </Button>
                    </div>
                  </div>
                </div>
              </ExpansionPanelDetails>
            </ExpansionPanel>
          </Grid>
        </Grid>
        <br />
        <DispatcherTable data={file} />
      </div>
    </div>
  );
}
