import React, { useState, useEffect, useContext } from "react";
import {
  Container,
  MenuItem,
  TextField,
  Button,
  Divider,
  ListItem,
  Chip,
  Typography,
} from "@material-ui/core";
import moment from "moment";
import { Autocomplete } from "@material-ui/lab";
import MuiAlert from "@material-ui/lab/Alert";
import TextMaskCustom from "../components/InputScreen/TextMaskCustom";
import StickyHeadTable from "../components/Table";
import ProdutividadeRealTime from "../components/ProdutividadeRealTime/ProdutividadeRealTime";
import axios from "axios";
import ProfileContext from "../context/profile/profileContext";
import { useSnackbar } from "notistack";
import Snackbar from "@material-ui/core/Snackbar";
import momentBusines from "moment-business-time";
import { sl } from "date-fns/esm/locale";

momentBusines.locale("en", {
  holidays: [
    "*-06-11",
    "*-01-01",
    "*-04-10",
    "*-04-21",
    "*-05-01",
    "*-07-07",
    "*-10-12",
    "*-11-02",
    "*-11-15",
    "*-12-25",
  ],
});

function ListItemLink(props) {
  return <ListItem button component="a" {...props} />;
}

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const qtd = [];
for (let i = 1; i <= 20; i++) {
  qtd.push(i);
}
export default function InputScreen() {
  moment().locale("pt-br");
  //Inicia instancia da notificao
  const { enqueueSnackbar } = useSnackbar();
  //Pega informaes do usuario logado
  const { api, userProfile, dev, getUserProfile } = useContext(ProfileContext);
  const apiURL = dev ? "https://localhost:7376" : "";
  const initialState = {
    numeroChamado: "",
    qtdchamados: 1,
    inicioTratamento: moment().format("DD/MM/YYYY HH:mm:ss"),
    fimTratamento: moment().format("DD/MM/YYYY HH:mm:ss"),
    origem: "Abertos",
    fcr: "",
    notificacao: "",
    dataCalculo: moment().format("YYYY-MM-DDTHH:mm:ss"),
    SLA: "",
    vencimentoSLA: "",
    vencimento: "",
    dataAbertura: "",
    tipoAtuacao: "Abertura",
    categoria: "",
    subCategoria: "",
    direcionamento: "",
    tratadoPor: "",
    productTipe: "",
  };

  //***Criao de states***
  const [produtos, setProdutos] = useState([]);
  const [subProduto, setSubProduto] = useState([]);
  const [values, setValues] = useState(initialState);
  const [tempoCallback, setTempoCallback] = useState("00:00:00");
  const [qtdChamados, setQtdChamados] = useState(0);
  const [table, settable] = useState([]);
  const [disableNext, setDisableNext] = useState(false);
  const [origem, setorigem] = useState([]);
  const [tipoAtuacao, settipoAtuacao] = useState([]);
  const [chamadosTratados, setChamadosTratados] = useState([]);
  const [productTipe, setproductTipe] = useState([]);
  const [torres, setTorres] = useState([]);
  const [state, setstate] = useState([]);
  const [vencSLA, setvencSLA] = useState("");
  const [open, setOpen] = useState(false);

  //***Fim da criao de States***

  //Funo que busca produtividade atual do analista

  useEffect(() => {
    getProdutividade();
  }, [userProfile]);

  const getProdutividade = async () => {
    await axios
      .get(
        apiURL +
          `/api/getdados/getprod?analista=${
            userProfile.email
          }&date=${moment().format("DD/MM/YY")}`
      )
      .then((resp) => {
        console.log("Quantidade chamados", resp.data.length);
        console.log("Chamados Tratados", resp.data);
        setChamadosTratados(resp.data);
        setQtdChamados(resp.data.length);
      })
      .catch((err) => console.log(err));
  };

  useEffect(() => {
    const prod = [];
    torres.map(
      (e) => (
        console.log(prod.indexOf(e.TipoDeProduto)),
        prod.indexOf(e.TipoDeProduto) === -1 ? prod.push(e.TipoDeProduto) : ""
      )
    );
    console.log(prod);
    setproductTipe(prod);
  }, [torres]);

  //Consulta lista de produtos
  useEffect(() => {
    if (api) getUserProfile();
    const getSelect = async () => {
      await axios
        .get(apiURL + "/api/input/getselec")
        .then((resp) => {
          console.log(resp.data);
          setorigem(resp.data.dados.origem.origem);
          settipoAtuacao(resp.data.dados.tipoAtuacao.tipoAtuacao);
          setProdutos(resp.data.dados.produtos.produtos);
          setTorres(resp.data.dados.torres.torres);
        })
        .catch((err) => console.error("Log do erro", err));
    };
    getSelect();
    console.log(
      momentBusines(moment().format())
        .addWorkingTime(24, "hours")
        .format("DD/MM/YYYY HH:mm:ss")
    );
    // console.log(moment().format());
  }, []);

  //Busca subprodutos de acordo com o produto selecionado
  useEffect(() => {
    console.log(
      "subprodutos",
      produtos.filter((item) => item.nome === values.categoria)
    );

    let temp = produtos.filter((item) => item.nome === values.categoria);
    console.log(temp);
    if (temp.length > 0) {
      const prod = torres.filter(
        (item) => item.TipoDeProduto === temp[0].TipodeChamado
      );
      console.log(prod);
      const uniqProd = [];
      const sla = [];
      prod.map(
        (e) => (
          console.log(prod.indexOf(e.TipoDeProduto)),
          uniqProd.indexOf(e.NomeDoGrupo) === -1
            ? (uniqProd.push(e.NomeDoGrupo), sla.push(e))
            : ""
        )
      );
      if (uniqProd.length > 0) {
        setstate(uniqProd);
        setproductTipe(sla);
        setValues({
          ...values,
          direcionamento: "",
          SLA: "",
        });
      } else {
        setstate([]);
        setValues({
          ...values,
          direcionamento: "",
          SLA: "",
        });
      }
    }
    if (temp[0] !== undefined) setSubProduto(temp[0].subprodutos);
  }, [values.categoria]);

  // pegar o sla da torre em questo
  useEffect(() => {
    // const prod = torres.filter(
    // 	(item) => item.TipoDeProduto === values.productTipe
    //   );
    console.log(productTipe);
    const sla = torres.filter(
      (e) =>
        e.TipoDeProduto === productTipe[0].TipoDeProduto &&
        e.NomeDoGrupo === values.direcionamento
    );
    console.log(sla);
    console.log(
      sla.length === 1 ? moment(sla[0].SLAPontual).isWorkingDay() : ""
    );
    // eslint-disable-next-line no-unused-expressions
    sla.length === 1 ? setValues({ ...values, SLA: sla[0].SLAPontual }) : "";
    console.log(sla.length === 1 ? sla[0].SLAPontual.split(":")[0] : "");
    if (values.dataCalculo !== "" && sla.length === 1) {
      let date = new Date(values.dataCalculo);
      console.log(date);
      console.log(
        moment(date).format("YYYY-MM-DD HH:mm:ss") +
          "  " +
          momentBusines(
            moment(values.dataCalculo).format("YYYY-MM-DD HH:mm:ss")
          )
            .addWorkingTime(parseInt(sla[0].SLAPontual.split(":")[0]), "hours")
            .format("DD/MM/YYYY HH:mm:ss") +
          "  " +
          moment().format()
      );

      setvencSLA(
        momentBusines(moment(values.dataCalculo).format("YYYY-MM-DD HH:mm:ss"))
          .addWorkingTime(parseInt(sla[0].SLAPontual.split(":")[0]), "hours")
          .format("DD/MM/YYYY HH:mm:ss")
      );
      //   10/06/2020 03:04:50
    }
  }, [values.direcionamento]);

  useEffect(() => {
    if (values.dataCalculo !== "" && values.SLA !== "") {
      setvencSLA(
        momentBusines(moment(values.dataCalculo).format("YYYY-MM-DD HH:mm:ss"))
          .addWorkingTime(parseInt(values.SLA.split(":")[0]), "hours")
          .format("DD/MM/YYYY HH:mm:ss")
      );
    }
  }, [values.dataCalculo]);

  //Calcula vencimento do chamado
  useEffect(() => {
    let vencimento = moment(values.dataAbertura, "DD/MM/YYYY HH:mm:ss")
      .add(1, "hours")
      .format("DD/MM/YYYY HH:mm:ss");
    if (vencimento === "Invalid date") vencimento = "";
    setValues({ ...values, vencimento });
  }, [values.dataAbertura]);

  //***Funes de alterao dos states***
  const handleClick = (variant, message) => {
    enqueueSnackbar(message, { variant });
  };

  const handleChange = (name) => (event) => {
    setValues({
      ...values,
      [name]: event.target.value,
    });
  };

  const handleOnChange = (name) => (e) => {
    setValues({ ...values, [name]: e.target.value });
    // console.log("DATES", arr);
  };

  const handleAutoCompleteChange = (name) => (event, value) => {
    setValues({
      ...values,
      [name]: value,
    });
  };
  const handleDisableNext = (value) => setDisableNext(value);

  //***Funes de alterao dos states***
  function isEmpty(obj) {
    for (var prop in obj) {
      console.log(obj);
      if (
        prop === "tipoAtuacao" ||
        prop === "qtdchamados" ||
        prop === "origem" ||
        prop === "categoria" ||
        prop === "notificacao" ||
        prop === "dataAbertura" ||
        prop === "fcr" ||
        prop === "subCategoria" ||
        prop === "tmo"
      ) {
        if (obj[prop] === "") {
          return false;
        }
      }
    }
    return true;
  }
  //Salvar chamado
  const handleSubmit = async () => {
    handleClick("info", "Salvando chamado...");
    setTimeout(async () => {
      var a = moment(
        moment().format("DD/MM/YYYY HH:mm:ss"),
        "DD/MM/YYYY HH:mm:ss"
      );
      var b = moment(values.inicioTratamento, "DD/MM/YYYY HH:mm:ss");
      console.log(a);
      console.log(b);
      let diff = moment.utc(a.diff(b, "seconds") * 1000).format("HH:mm:ss");
      const data = {
        ...values,
        email: userProfile.email,
        fimTratamento: moment().format("DD/MM/YYYY HH:mm:ss"),
        tmo: diff,
      };
      if (isEmpty(data)) {
        console.log("Dados enviados: ", data);
        await axios
          .post(apiURL + "/api/input/inputecmg", data)
          .then((resp) => {
            handleDisableNext(false);
            handleClick("success", "Enviado Chamado");
            setValues({
              id: "",
              numeroChamado: "",
              qtdchamados: 1,
              inicioTratamento: moment().format("DD/MM/YYYY HH:mm:ss"),
              fimTratamento: moment().format("DD/MM/YYYY HH:mm:ss"),
              origem: "Abertos",
              fcr: "",
              notificacao: "",
              dataCalculo: moment().format("YYYY-MM-DD HH:mm:ss"),
              SLA: "",
              vencimentoSLA: "",
              vencimento: "",
              dataAbertura: "",
              tipoAtuacao: tipoAtuacao[0],
              categoria: produtos[0].nome,
              subCategoria: subProduto[0],
              direcionamento: "",
              tratadoPor: userProfile.displayName,
            });
          })
          .catch((err) => {
            console.error("Log de erro:", err);
            handleDisableNext(false);
            handleClick("error", "Falha no salvamento");
          });
        setChamadosTratados([...chamadosTratados, data]);
      } else {
        alert("Para salvar no pode ter campos em branco");
        handleClick("error", "Falha no salvamento");
      }
    }, 500);
  };

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  //Buscar prximo chamado
  const netx = async () => {
    console.log("Clicado Proximo Chamado");
    getProdutividade();
    await axios
      .get(apiURL + `/api/getdados/nextCalled?analista=${userProfile.email}`)
      .then((resp) => {
        setValues({
          id: resp.data[0]._id,
          numeroChamado: "",
          qtdchamados: 1,
          inicioTratamento: moment().format("DD/MM/YYYY HH:mm:ss"),
          fimTratamento: moment().format("DD/MM/YYYY HH:mm:ss"),
          origem: resp.data[0].origem,
          fcr: "",
          notificacao: resp.data[0].notificacao,
          dataCalculo: "",
          SLA: "",
          vencimentoSLA: "",
          vencimento: "",
          dataAbertura: resp.data[0].dataAbertura,
          tipoAtuacao: tipoAtuacao[0],
          categoria: produtos[0].nome,
          subCategoria: subProduto[0],
          direcionamento: "",
          tratadoPor: userProfile.displayName,
        });
        console.log("Proximo Chamado", resp.data);
        if (resp.data[0].prioridade === 2) {
          handleClick("info", "Chamado prioriza");
          setOpen(true);
        }
        handleDisableNext(true);
      })
      .catch((err) => console.error("Log do erro", err));
  };

  var regex = /((INC)|(ECS)|(RITM))((\d|_)(\d{8}|(\d{7}_\d{1,2})))/;

  return (
    <div className="container-fluid p-0">
      <nav
        className="border border-light bg-light shadow d-flex justify-content-center"
        style={{ height: "60px" }}
      >
        <Typography variant="h5" className="align-self-center">
           comigo Santander
        </Typography>
      </nav>
      <br />
      <div className="container-fluid">
        <Container
          disableGutters={true}
          maxWidth="xl"
          className="container-fluid"
        >
          <ProdutividadeRealTime
            tempoCallBack={tempoCallback}
            quantidadeChamados={qtdChamados}
          />
          <form className="m-2 needs-validation">
            <div className="form-row">
              <div className="col-xl-3 mb-3">
                <TextField
                  className="form-control"
                  size="small"
                  value={values.numeroChamado}
                  error={regex.test(values.numeroChamado) ? false : true}
                  id="numberChamado"
                  label="Chamado"
                  variant="outlined"
                  onChange={(e) => {
                    let numChamado =
                      regex.exec(e.target.value) !== null
                        ? regex.exec(e.target.value)[0]
                        : e.target.value;
                    setValues({ ...values, numeroChamado: numChamado });
                  }}
                />
              </div>
              <div className="col-xl-2 mb-3">
                <Autocomplete
                  id="atuacaoType"
                  size="small"
                  options={tipoAtuacao}
                  getOptionLabel={(option) => option}
                  defaultValue={tipoAtuacao[0]}
                  onChange={handleAutoCompleteChange("tipoAtuacao")}
                  renderTags={(value, getTagProps) =>
                    value.map((option, index) => (
                      <Chip
                        variant="outlined"
                        label={option}
                        size="small"
                        {...getTagProps({ index })}
                      />
                    ))
                  }
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      variant="outlined"
                      label="Tipo de atuao"
                      placeholder="Selecione"
                    />
                  )}
                />
              </div>
              <div className="col-xl-2 mb-3">
                <TextField
                  className="form-control"
                  id="qtdChamados"
                  size="small"
                  label="Quantidade chamados"
                  defaultValue={qtd[0]}
                  variant="outlined"
                  select
                >
                  {qtd.map((i) => (
                    <MenuItem key={i} value={i}>
                      {i}
                    </MenuItem>
                  ))}
                </TextField>
              </div>
              <div className="col-xl-2 mb-3">
                <Autocomplete
                  id="origem"
                  size="small"
                  options={origem}
                  getOptionLabel={(option) => option}
                  defaultValue={values.origem}
                  onChange={handleAutoCompleteChange("origem")}
                  renderTags={(value, getTagProps) =>
                    value.map((option, index) => (
                      <Chip
                        variant="outlined"
                        label={option}
                        size="small"
                        {...getTagProps({ index })}
                      />
                    ))
                  }
                  renderInput={(params) => (
                    <TextField {...params} variant="outlined" label="Origem" />
                  )}
                />
              </div>
              <div className="col-xl-3 mb-3">
                <Autocomplete
                  id="categoria"
                  size="small"
                  options={produtos.map((item) => item.nome)}
                  getOptionLabel={(option) => option}
                  onChange={handleAutoCompleteChange("categoria")}
                  renderTags={(value, getTagProps) =>
                    value.map((option, index) => (
                      <Chip
                        variant="outlined"
                        label={option}
                        size="small"
                        {...getTagProps({ index })}
                      />
                    ))
                  }
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      variant="outlined"
                      label="Categoria"
                    />
                  )}
                />
              </div>
            </div>
            <div className="form-row">
              <div className="col-xl-3 mb-3">
                <TextField
                  className="form-control"
                  size="small"
                  value={values.notificacao}
                  id="notification"
                  label="Notificao"
                  variant="outlined"
                  onChange={handleChange("notificacao")}
                />
              </div>
              <div className="col-xl-2 mb-3">
                <TextField
                  className="form-size"
                  size="small"
                  variant="outlined"
                  label="Data de abertura"
                  value={values.dataAbertura}
                  onChange={handleChange("dataAbertura")}
                  id="formatted-numberformat-input"
                  InputProps={{
                    inputComponent: TextMaskCustom,
                  }}
                />
              </div>
              <div className="col-xl-2 mb-3">
                <TextField
                  className="form-size"
                  size="small"
                  variant="outlined"
                  label="Vencimento"
                  value={values.vencimento}
                  onChange={handleChange("vencimento")}
                  id="vencimento"
                  InputProps={{
                    inputComponent: TextMaskCustom,
                  }}
                />
              </div>
              <div className="col-xl-2 mb-3">
                <TextField
                  className="form-control"
                  size="small"
                  value={values.fcr}
                  id="fcr"
                  label="FCR"
                  variant="outlined"
                  onChange={handleChange("fcr")}
                />
              </div>

              <div className="col-xl-3 mb-3">
                <Autocomplete
                  id="prod"
                  size="small"
                  options={subProduto}
                  getOptionLabel={(option) => option}
                  onChange={handleAutoCompleteChange("subCategoria")}
                  renderTags={(value, getTagProps) =>
                    value.map((option, index) => (
                      <Chip
                        variant="outlined"
                        label={option}
                        size="small"
                        {...getTagProps({ index })}
                      />
                    ))
                  }
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      variant="outlined"
                      label="Subcategoria"
                    />
                  )}
                />
              </div>
            </div>
          </form>
          <Divider />
          <form className="m-2 pt-3">
            <div className="form-row">
              <div className="col-xl-2 mb-3">
                {/* <TextField
                className="form-size"
                size="small"
                variant="outlined"
                label="Data e hora para calculo"
                type="datetime-local"
                value={values.dataCalculo}
                onChange={handleOnChange("dataCalculo")}
                id="dataCalculo"
                InputProps={{
                  inputComponent: TextMaskCustom,
                }}
              /> */}
                <TextField
                  id="from-date"
                  label="Data e hora para calculo"
                  type="datetime-local"
                  variant="outlined"
                  defaultValue={values.dataCalculo}
                  size="small"
                  className="form-size"
                  InputLabelProps={{
                    shrink: true,
                  }}
                  onChange={handleOnChange("dataCalculo")}
                />
              </div>
              <div className="col-xl-2 mb-3">
                <TextField
                  className="form-control"
                  size="small"
                  value={values.SLA}
                  id="sla"
                  label="SLA"
                  variant="outlined"
                  onChange={handleChange("SLA")}
                />
              </div>
              <div className="col-xl-2 mb-3">
                <TextField
                  className="form-size"
                  size="small"
                  variant="outlined"
                  label="Vencimento SLA"
                  value={vencSLA}
                  onChange={handleChange("vencimentoSLA")}
                  id="venciSLA"
                  InputProps={{
                    inputComponent: TextMaskCustom,
                  }}
                />
              </div>
              <div className="col-xl-3 mb-3">
                <Autocomplete
                  id="pr"
                  size="small"
                  options={state}
                  getOptionLabel={(option) => option}
                  // defaultValue={}
                  value={values.direcionamento}
                  clearOnEscape
                  onChange={handleAutoCompleteChange("direcionamento")}
                  renderTags={(value, getTagProps) =>
                    value.map((option, index) => (
                      <Chip
                        variant="outlined"
                        label={option}
                        size="small"
                        {...getTagProps({ index })}
                      />
                    ))
                  }
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      variant="outlined"
                      label="Direcionamento"
                    />
                  )}
                />
              </div>
              <div className="col-xl-2 mb-3 d-flex align-items-center">
                <Button
                  variant="outlined"
                  color="primary"
                  size="large"
                  onClick={handleSubmit}
                >
                  Salvar
                </Button>
                <Button
                  variant="outlined"
                  color="primary"
                  size="large"
                  onClick={netx}
                  className="ml-2"
                  disabled={disableNext}
                >
                  Prximo
                </Button>
              </div>
            </div>
          </form>
          <Divider />
          <div className="row m-2 pt-2">
            <StickyHeadTable rowsArray={chamadosTratados} />
          </div>
        </Container>
        <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
          <Alert onClose={handleClose} severity="success">
            Chamado prioriza
          </Alert>
        </Snackbar>
        ;
      </div>
    </div>
  );
}
