import React, { useState, useEffect, useContext } from "react";
import {
  TextField,
  Select,
  MenuItem,
  Button,
  Typography,
  InputLabel,
  FormControl,
  GridList,
  GridListTile,
} from "@material-ui/core";
import Input from "@material-ui/core/Input";
import MailIcon from "@material-ui/icons/Mail";
import Fab from "@material-ui/core/Fab";
import AddIcon from "@material-ui/icons/Add";
import MaterialTable from "material-table";
import ProfileContext from "../context/profile/profileContext";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import axios from "axios";
import LinearProgress from "@material-ui/core/LinearProgress";

export default function CadastroUsuario() {
  const { dev, getUserProfile, api, userProfile } = useContext(ProfileContext);
  const apiURL = dev ? "https://localhost:7376" : "";
  const [update, setupdate] = useState(null);
  const [tipo, setTipo] = useState(["Analista", "Lider"]);
  const [addressPhoto, setAddressPhoto] = useState("");
  const [open, setOpen] = useState(false);
  const [nameDisabled, setNameDisabled] = useState(false);
  const [dblock, setdblock] = useState("d-none");
  const [foto, setfoto] = useState();
  const [form, setform] = useState({
    displayName: "",
    email: "",
    profileType: "",
    team: "",
    clients: [],
    profile_pic: "",
  });
  const columns = [
    {
      title: "Avatar",
      field: "_attachments",
      render: (rowData) => (
        <img
          src={`${apiURL}/api/user/profile_pic?id=${rowData.email}`}
          style={{ width: 40, borderRadius: "50%" }}
          alt=""
        />
      ),
    },
    {
      title: "Nome Analista",
      field: "displayName",
    },
    {
      title: "E-mail Analista",
      field: "email",
    },
    {
      title: "Operao",
      field: "clients",
    },
    {
      title: "Tipo de Perfil",
      field: "profileType",
    },
  ];
  const [gridData, setGridData] = useState({
    data: [],
    columns: columns,
    resolve: () => {},
    updatedAt: new Date(),
  });
  const [rows, setRows] = useState([]);
  const t = {
    resolve: () => {},
    updatedAt: new Date(),
  };

  let options = [
    { value: "Automation Team", label: "Automation Team" },
    { value: "IGA", label: "IGA" },
    { value: "Santander", label: "Santander" },
    { value: "Help Center", label: "Help Center" },
    { value: "dpsp", label: "Dpsp" },
    { value: "Via Varejo", label: "Via Varejo" },
  ];

  useEffect(() => {
    if (api) getUserProfile();
  }, []);

  useEffect(() => {
    const getDados = async () => {
      await axios
        .get(`${apiURL}/api/team/users?team=${userProfile.clients}`)
        .then(
          (resp) =>
            new Promise((resolve, reject) => {
              console.log(resp.data.docs);
              let data = [...gridData.data];
              data = resp.data.docs;
              console.log(data);
              const updatedAt = new Date();
              setGridData({ ...gridData, data, updatedAt, resolve });
            })
        )
        .catch((err) => console.log(err));
    };
    if (userProfile.profileType === "leader") {
      setTipo([
        <option value=""></option>,
        <option value="leader">Lder</option>,
        <option value="user">Analista</option>,
      ]);
    } else {
      setTipo([
        <option value=""></option>,
        <option value="admin">Administrador</option>,
        <option value="leader">Lder</option>,
        <option value="user">Analista</option>,
      ]);
    }
    getDados();
  }, [userProfile, update]);

  useEffect(() => {
    console.log(gridData);
  }, [gridData]);

  useEffect(() => {
    t.resolve();
    console.log("RESOLVE AT:", t.updatedAt);
    console.log(rows);
  }, [rows]);

  const onRowUpdate = (newData, oldData) =>
    new Promise((resolve, reject) => {
      // Copy current state data to a new array
      const data = [...rows];
      // Get edited row index
      const index = data.indexOf(oldData);
      // replace old data
      data[index] = newData;
      // update state with the new array
      const updatedAt = new Date();
      console.log(data);
      setRows([...rows, data]);
      setTimeout(() => {
        console.log(rows);
      }, 1000);
    });

  const onRowDelete = (oldData) =>
    new Promise((resolve, reject) => {
      let data = [...rows];
      const index = data.indexOf(oldData);
      data.splice(index, 1);
      const updatedAt = new Date();
      setRows({ ...rows, data, updatedAt, resolve });
    });

  const onRowAdd = (newData) =>
    new Promise((resolve, reject) => {
      const data = [...rows];
      data.push(newData);
      const updatedAt = new Date();
      setRows({ ...rows, data });
    });

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const getProfile = async (e) => {
    setdblock("d-block");
    let value = e.target.value;
    if (value.indexOf("ibm.com") > 0) {
      setNameDisabled(true);
      var res = await axios.get(
        "https://w3-services1.w3-969.ibm.com/myw3/unified-profile/v1/api/find?q=" +
          e.target.value
      );
      setNameDisabled(false);
      setform({ ...form, displayName: res.data[0].name });
      validationPhotoBuepage();
    } else {
      console.log("funcionario nao ibm");
    }
    setdblock("d-none");
  };

  function validationPhotoBuepage() {
    axios
      .get(
        `https://w3-services1.w3-969.ibm.com/myw3/unified-profile-photo/v1/image/${form.email}`
      )
      .then((response) => {
        if (response.data === "") {
          setAddressPhoto("");
        } else {
          fetch(
            `https://w3-services1.w3-969.ibm.com/myw3/unified-profile-photo/v1/image/${form.email}`,
            {
              headers: {
                Accept: "image/jpeg",
              },
              responseType: "blob",
            }
          )
            .then((response) => {
              if (response.ok) {
                return response.blob();
              }
            })
            .then(async (blob) => {
              var newBlob = new Blob([blob], { type: "image/jpg" });
              var newFile = new File([newBlob], "profile_pic.jpg", {
                type: "image/jpg",
              });
              setfoto(newFile);
              // setform({ ...form, profile_pic: newFile });
            });
          setAddressPhoto(
            `https://w3-services1.w3-969.ibm.com/myw3/unified-profile-photo/v1/image/${form.email}`
          );
        }
      })
      .catch((error) => {
        setAddressPhoto("");
      });
  }

  async function handleSubmit() {
    const formdate = {
      ...form,
      profile_pic: foto,
    };
    var formData = new FormData();
    formData.append("displayName", formdate.displayName);
    formData.append("email", formdate.email);
    formData.append("profileType", formdate.profileType);
    formData.append("team", formdate.team);
    formData.append("profile_pic", formdate.profile_pic);
    formData.append("clients", formdate.clients);
    console.log(formdate.clients);
    // formData.delete("profile_pic");
    // formData.append("profile_pic", foto);

    await axios
      .post(apiURL + "/api/user/new", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then(function () {
        setupdate(true);
        handleClose();
        console.log("Cadastro com Sucesso");
      })
      .catch(function (response) {
        console.log(response);
      });
  }

  return (
    <div className="container-fluid p-0">
      <nav
        className="border border-light bg-light shadow d-flex justify-content-center"
        style={{ height: "60px" }}
      >
        <Typography variant="h5" className="align-self-center">
          Cadastro de Usuario
        </Typography>
      </nav>
      <div className="container-fluid">
        <br />
        <div className="row justify-content-center">
          <div className="col-11">
            <MaterialTable
              title="Analistas Cadastrados"
              className=""
              columns={gridData.columns}
              data={gridData.data}
              editable={{
                isEditable: (rowData) => true,
                isDeletable: (rowData) => true,
                onRowUpdate: onRowUpdate,
                onRowDelete: onRowDelete,
              }}
              options={{
                exportButton: true,
                actionsColumnIndex: -1,
              }}
              actions={[
                {
                  icon: () => (
                    <Fab size="small" color="secondary" aria-label="add">
                      <AddIcon />
                    </Fab>
                  ),
                  tooltip: "Add User",
                  isFreeAction: true,
                  onClick: (event) => handleOpen(),
                },
              ]}
            />
          </div>
        </div>
      </div>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
        fullWidth
      >
        <DialogTitle id="form-dialog-title">
          Cadastro de Novo Usurio
        </DialogTitle>
        <DialogContent>
          <form action="" noValidate autoComplete="off">
            <div className="form-row mb-2">
              <div className="col">
                <TextField
                  label="Email"
                  className="form-control"
                  name="email"
                  id="emailAnalista"
                  variant="outlined"
                  size="small"
                  value={form.email}
                  onBlur={(e) => getProfile(e)}
                  onChange={(e) => setform({ ...form, email: e.target.value })}
                />
                <div className={dblock}>
                  <LinearProgress />
                </div>
              </div>
            </div>
            <div className="form-row mb-2">
              <div className="col">
                <TextField
                  label="Nome"
                  className="form-control"
                  name="nome"
                  id="nomeAnalista"
                  variant="outlined"
                  size="small"
                  value={form.displayName}
                  disabled={nameDisabled}
                  onChange={(e) =>
                    setform({ ...form, displayName: e.target.value })
                  }
                />
              </div>
            </div>
            <div className="form-row mb-2">
              <div className="col">
                <FormControl
                  variant="outlined"
                  size="small"
                  style={{ minWidth: "100%" }}
                >
                  <InputLabel htmlFor="outlined-age-native-simple">
                    Operao
                  </InputLabel>
                  <Select
                    native
                    value={form.team}
                    onChange={(e) => setform({ ...form, team: e.target.value })}
                    label="Operao"
                  >
                    <option aria-label="None" value="" />
                    <option value={"Web"}>WEB</option>
                    <option value={"ECMG"}> Comigo</option>
                    <option value={"IT"}>IT Support</option>
                    <option value={"Prime"}>Prime</option>
                  </Select>
                </FormControl>
              </div>
            </div>
            <div className="form-row mb-2">
              <div className="col">
                <FormControl
                  variant="outlined"
                  size="small"
                  style={{ minWidth: "100%" }}
                >
                  <InputLabel htmlFor="outlined-age-native-simple">
                    Tipo de perfil
                  </InputLabel>
                  <Select
                    native
                    value={form.profileType}
                    onChange={(e) =>
                      setform({ ...form, profileType: e.target.value })
                    }
                    label="Tipo de perfil"
                  >
                    {tipo.map((e) => {
                      return e;
                    })}
                  </Select>
                </FormControl>
              </div>
            </div>
            <div className="form-row mb-2">
              <div className="col">
                <FormControl
                  variant="outlined"
                  size="small"
                  style={{ minWidth: "100%" }}
                >
                  <InputLabel id="clientes">Clientes</InputLabel>
                  <Select
                    labelId="clientes"
                    id="clientes"
                    multiple
                    label="Clientes"
                    value={form.clients}
                    onChange={(e) =>
                      setform({ ...form, clients: e.target.value })
                    }
                  >
                    {options.map((name) => (
                      <MenuItem key={name.value} value={name.value}>
                        {name.label}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </div>
            </div>
            <div className="form-row mb-2 justify-content-center">
              <div className="row justify-content-center">
                <br />
                <GridList cols={1}>
                  <GridListTile key={form.displayName} col={1} rows={2}>
                    <img
                      src={addressPhoto}
                      alt={form.displayName}
                      className="img-thumbnail"
                      max-width="200px"
                      max-height="150px"
                      width="auto"
                      height="auto"
                    />
                  </GridListTile>
                </GridList>
              </div>
            </div>
          </form>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} variant="outlined" color="secondary">
            Cancel
          </Button>
          <Button onClick={handleSubmit} variant="outlined" color="primary">
            Salvar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
