import React from "react";
import { Switch, Route } from "react-router";
import ClientList from "../Views/ClientList.jsx";
import InputScreen from "../Views/InputScreen.jsx";
import Dispatcher from "../Views/Dispatcher.jsx";
import InputScreenWeb from "../Views/InputScreenWeb.jsx";
import Leadership from "../Views/Leadership.jsx";
import CadastroProduto from "../Views/CadastroProduto.jsx";
import CadastroUsuario from "../Views/CadastroUsuario.jsx";
import Dashboard from "../Views/Dashboard.jsx";
import Teste from "../Views/teste";
import Reports from "../Views/Reports";

const Routes = () => (
  <Switch>
    <Route exact path="/" component={Teste} />
    <Route path="/InputScreen" component={InputScreen} />
    <Route path="/InputScreenWeb" component={InputScreenWeb} />
    <Route path="/Dispatcher" component={Dispatcher} />
    <Route path="/Leadership" component={Leadership} />
    <Route path="/CadastroProduto" component={CadastroProduto} />
    <Route path="/CadastroUsuario" component={CadastroUsuario} />
    <Route path="/Dashboard" component={Dashboard} />
    <Route path="/reports" component={Reports} />
  </Switch>
);

export default Routes;
